@import url(https://fonts.googleapis.com/css?family=Oxanium&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Oxanium", Arial, Helvetica, sans-serif;
  background-image: linear-gradient(0.3turn, #d393be, #101b58);
}

.main {
  min-height: 100vh;
  width: 80%;
  margin: 70px auto;
}

@media (max-width: 900px) {
  .main {
    width: 100%;
  }
}

.infoArea {
  box-shadow: 4px 6px 10px 0px #1c0338;
  padding: 10px;
  width: 80%;
  margin: 0 auto;
  text-align: center;
  background-image: -webkit-linear-gradient(0.3turn, #50073a, #04033d);
}

.infoArea__balance {
  margin-top: 15px;
  font-size: 4em;
  color: #e5d8e7;
}

.container {
  margin-top: 50px;
  display: flex;
  justify-content: space-around;
}

@media (max-width: 900px) {
  .container {
    flex-wrap: wrap;
  }
}

.expenses,
.income {
  background-color: #f8e9fa;
  box-shadow: 10px 12px 15px 0px #26043d;
  text-align: center;
  margin: 0 10px;
  min-width: 320px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

@media (max-width: 900px) {
  .expenses,
  .income {
    width: 80%;
    max-width: 500px;
    margin: 40px;
  }
}

.expenses__container .simplebar-track.simplebar-vertical,
.income__container .simplebar-track.simplebar-vertical {
  width: 12px;
  opacity: 0.5;
}

.expenses .list-element,
.income .list-element {
  padding: 5px;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  min-height: 80px;
  text-transform: capitalize;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}

.expenses .list-element__date,
.income .list-element__date {
  flex-basis: 60%;
  display: flex;
  font-size: 0.9em;
  flex-grow: 1;
  opacity: 0.7;
}

.expenses .list-element__bin,
.income .list-element__bin {
  color: #5c555c;
  margin: 0 5px;
  font-size: 1.6em;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.expenses .list-element__bin:hover,
.income .list-element__bin:hover {
  font-size: 1.8em;
  color: black;
  cursor: pointer;
}

.expenses .list-element__title,
.income .list-element__title {
  display: flex;
  margin: 0 10px;
  flex-basis: 60%;
  font-size: 1.6em;
  font-weight: bold;
  opacity: 0.8;
  align-items: flex-end;
}

.expenses .list-element__value,
.income .list-element__value {
  display: flex;
  justify-content: flex-end;
  margin: 0 10px;
  flex-grow: 1;
  font-size: 1.2em;
  opacity: 0.9;
  align-items: flex-end;
}

.expenses .list-element:nth-child(odd),
.income .list-element:nth-child(odd) {
  background-color: rgba(65, 4, 80, 0.123);
}

.expenses__number, .expenses__textfield,
.income__number,
.income__textfield {
  width: 60%;
}

.expenses__number input[type="number"]::-webkit-inner-spin-button,
.expenses__number input[type="number"]::-webkit-outer-spin-button, .expenses__textfield input[type="number"]::-webkit-inner-spin-button,
.expenses__textfield input[type="number"]::-webkit-outer-spin-button,
.income__number input[type="number"]::-webkit-inner-spin-button,
.income__number input[type="number"]::-webkit-outer-spin-button,
.income__textfield input[type="number"]::-webkit-inner-spin-button,
.income__textfield input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.expenses__number input[type="number"], .expenses__textfield input[type="number"],
.income__number input[type="number"],
.income__textfield input[type="number"] {
  -moz-appearance: textfield;
}

.expenses__styledHeader,
.income__styledHeader {
  font-weight: bold;
  margin-top: 15px;
  font-size: 3.2em;
  background: -webkit-linear-gradient(0.3turn, #7e116c, black);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.expenses__summedValue,
.income__summedValue {
  font-size: 1.2em;
  margin-top: 10px;
  opacity: 0.8;
}

.button {
  border-radius: 0;
  font-size: 1em;
  margin: 20px auto;
  padding: 5px 40px;
  letter-spacing: 0.4em;
  background-image: -webkit-linear-gradient(0.3turn, #50073a, #04033d);
}

.switch {
  position: absolute;
  max-width: 25px;
  max-height: 25px;
  width: 20px;
  height: 20px;
  opacity: 0.5;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  cursor: pointer;
  color: white;
  font-size: 1.2em;
}

.switch:hover {
  font-size: 1.4em;
}

@media (max-width: 900px) {
  .switch {
    right: 80px;
    top: 10px;
    font-size: 2.6em;
  }
  .switch:hover {
    color: #ffffa3;
    font-size: 2.6em;
  }
}

.transition {
  -webkit-animation: trans 1s;
          animation: trans 1s;
}

@-webkit-keyframes trans {
  0% {
    opacity: 1;
  }
  40% {
    opacity: 0;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    box-shadow: none;
  }
  60% {
    opacity: 0;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
    opacity: 1;
  }
}

@keyframes trans {
  0% {
    opacity: 1;
  }
  40% {
    opacity: 0;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    box-shadow: none;
  }
  60% {
    opacity: 0;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
    opacity: 1;
  }
}
